<template>
  <nav class="navbar navbar-expand-lg navbar-light">
    <div class="container-fluid">
      
      <a class="navbar-brand" href="#">{{routeName}}</a>

      <button class="navbar-toggler navbar-burger"
              type="button"
              @click="toggleSidebar"
              :aria-expanded="$sidebar.showSidebar"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
      </button>
      <div class="collapse navbar-collapse">      
        <ul class="navbar-nav ml-auto">
          <li v-if="user" class="nav-item">
            <a href="#" class="nav-link">
              <i class="ti-user"></i>
              <p>{{user}}</p>
            </a>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link">
              <i class="ti-panel"></i>
              <p>Stats</p>
            </a>
          </li>
          <drop-down class="nav-item"
                     title="5 Notifications"
                     title-classes="nav-link"
                     icon="ti-bell">
            <a class="dropdown-item" href="#">Notification 1</a>
            <a class="dropdown-item" href="#">Notification 2</a>
            <a class="dropdown-item" href="#">Notification 3</a>
            <a class="dropdown-item" href="#">Notification 4</a>
            <a class="dropdown-item" href="#">Another notification</a>
          </drop-down>
          <li class="nav-item">
            <p-button v-if="autenticado" type="danger" round
            @click.native="logout()">
              Salir
            </p-button>
            
          </li>
        </ul>
      </div>
    </div></nav>
</template>
<script>

export default {
  computed: {
    routeName() {
      const { name } = this.$route;
      return this.capitalizeFirstLetter(name);
    }
  },
  data() {
    return {
      activeNotifications:  false,
      autenticado:          false,
      user:                 false
    };
  },
  created () {
    this.autenticado =localStorage.getItem('Verificacion');
    this.user =localStorage.getItem('User');
  },
  methods: {
    logout() {
      localStorage.removeItem('Verificacion');
      localStorage.removeItem('Login');
      localStorage.removeItem('Password');
      localStorage.removeItem('User');
      localStorage.removeItem('rol_adm');
      this.$router.go(0);
    },
    
    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    toggleNotificationDropDown() {
      this.activeNotifications = !this.activeNotifications;
    },
    closeDropDown() {
      this.activeNotifications = false;
    },
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$.showSidebar);
    },
    hideSidebar() {
      this.$sidebar.displaySidebar(false);
    }
  }
};
</script>
