<template>
  <div>
    <!--Stats cards-->
    <div class="row">
      <div class="col-md-6 col-xl-6" v-for="stats in statsCards" :key="stats.title">
        <stats-card>
          <div class="icon-big text-center" :class="`icon-${stats.type}`" slot="header">
            <i :class="stats.icon"></i>
          </div>
          <div class="icon-big text-center" slot="header">
            <div :class="`alert alert-${stats.type}`">
              <span>{{stats.etap}}</span>
            </div>
          </div>
          <div class="numbers" slot="content">
            <p>{{stats.value}}</p>
            {{stats.title}}
             <p>{{stats.descr}}</p>
          </div>

          <div class="numbers" slot="footer">
            <div class="progress">
                <div class="progress-bar" role="progressbar" style="width: 25%;" 
                  aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">25%</div>
            </div>
          </div>

          <div class="numbers" slot="footer">
            <p-button type="info">
              Invertir
            </p-button>

          </div>
          
          <div class="stats" slot="footer">
            <i :class="stats.footerIcon"></i> {{stats.footerText}}
          </div>
        </stats-card>
      </div>
    </div>

{{error}}
  </div>
</template>
<script>

import API from "../plugins/globalApi";


import { StatsCard, ChartCard } from "@/components/index";
import Chartist from 'chartist';
export default {
  components: {
    StatsCard,
    ChartCard
  },
  /**
   * Chart data used to render stats, charts. Should be replaced with server data
   */
  data() {
    return {
      correo: '',
      clave: '',
      correo_reg: '',
      clave_reg: '',
      clave_reg2: '',
      error: null, 
      autenticado: false,
      estatus: false,
      statsCards: [],
      etapa: ['En Proceso','Completo','Cerrado'],
      tipo: ['info','success','warning']

    };
  },
  created () {
    
    API({
      action:  'portafolio',
      correo:   localStorage.getItem('Login'),
      clave:    localStorage.getItem('Password')
    }).then(response => {
      //this.error = response.data.portafolio;
      response.data.portafolio.forEach(element => {
        this.statsCards.push({
          type: this.tipo[element.etapa],
          etap: this.etapa[element.etapa],
          icon: "ti-stats-up",
          title: element.titulo,
          descr: element.descripcion,
          value: "$"+element.meta,
          footerText: 'Fecha tope: '+element.fecha,
          footerIcon: "ti-time"
        });
      });
    });
  },
  methods: {
  }
};
</script>
