<template>
  <card class="card" title="Ingresar">
    {{this.login.error}}
    <div>
      <form @submit.prevent>
        <div class="row">
          <div class="col-md-12">
            <fg-input type="email"
                      label="Correo"
                      placeholder="Correo Electrónico"
                      v-model="login.correo">
            </fg-input>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <fg-input type="password"
                      label="Clave"
                      placeholder="Clave"
                      v-model="login.clave">
            </fg-input>
          </div>
        </div>
        <div class="text-center">
          <p-button type="success"
                    round
                    @click.native.prevent="Ingresar">
            Ingresar
          </p-button>
        </div>
        <div class="clearfix"></div>
      </form>
    </div>
  </card>
</template>
<script>

import API from "../plugins/globalApi";
import Mensaje from "../plugins/globalNotificaciones";

export default {
  data() {
    return {
      login: {
        correo: "",
        clave: "",
        error:    null,
        validar:  false,
        mens:     null
      }
    }
  },
  methods: {
    Ingresar() {
      if (this.login.correo.length==0 || this.login.clave.length==0) {
        this.$notify(Mensaje(3,'Debe ingresar los datos solicitados.'));
      } else if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/).test(this.login.correo)){
        this.$notify(Mensaje(3,'El correo no es válido'));
      } else {
        this.login.validar = true;
      }

      if (this.login.validar) {
        API({
          action:  'login',
          correo:   this.login.correo,
          clave:    this.login.clave
          }).then(response => {
            //this.login.error = response.data.user;
            if (response.data.error){
              this.$notify(Mensaje(3,response.data.message));
            } else {
              this.$notify(Mensaje(2,response.data.message));
              localStorage.setItem('Login', this.login.correo);
              localStorage.setItem('Password', this.login.clave);
              localStorage.setItem('User',response.data.user.nombre+' '+response.data.user.apellido);
              localStorage.setItem('Verificacion', response.data.authenticated);
              if (response.data.user.rol_adm>0){
                localStorage.setItem('rol_adm', true);
              }
              this.login.correo = '';
              this.login.clave = '';
              this.$router.go(0);
            }   
          }).catch(e => {console.log(e);})
       }
    }
  }
}
</script>